import { cloneDeep, isFunction } from 'lodash'
import { Component } from '../core/component'
import { clone } from '../base/util/clone'

import { Sequence, SequenceOptions } from './sequence'
import { sampleMode } from './util/sample'

const loopDefaults = {
  templateParameters: [],
  sample: {
    mode: 'sequential',
  },
  shuffleUngrouped: false,
}

export interface LoopOptions<T> extends SequenceOptions {
  /** Array of settings to generate components from */
  templateParameters: T[]
  /**
   * Component or builder function to generate a single iteration of the loop,
   * given a set of parameters
   */
  template:
    | Component
    | Component[]
    | ((parameters: T, index: number, parent: Component) => Component)
    | undefined
  /**
   * Optionally draw a sub- or superset of the template parameters prior
   * to populating the loop
   */
  sample: {
    /**
     * Number of samples to draw (can be larger than the number of
     * parameter sets)
     */
    n?: number
    /**
     * How to draw samples. Either
     * * `sequential`: Begin at the start and cut off at some point,
     *   (if the sample is smaller than the total) or continue from the start
     *   once the available templates have been exhausted. Either way, use
     *   the order in which the templates are given.
     * * `draw-replace`: Sample entries from the `templateParameters` with
     *   replacement
     * * `draw`: Draw samples from `templateParameters` without replacement.
     *   If `n` is larger than the number of available template sets,
     *   shuffle the template sets and append to fill the output as far as
     *   possible, then draw the remainder without replacement.
     * * `draw-shuffle`: Like `draw`, but shuffle the output one more time.
     */
    mode: 'sequential' | 'draw' | 'draw-shuffle' | 'draw-replace'
  }
  shuffleGroups?: string[][]
  shuffleUngrouped?: boolean
}

/**
 * Loop
 *
 * A loop functions exactly like a sequence, except that the components
 * in the loop are generated upon initialization from a factory function
 * and a data collection.  Technically, the content is generated by
 * mapping the data onto the factory function.
 */
export class Loop<T extends Record<string, any>> extends Sequence {
  options!: LoopOptions<T>

  constructor(options: Partial<LoopOptions<T>> = {}) {
    super({
      ...cloneDeep(loopDefaults),
      ...options,
    })
  }

  onPrepare() {
    let templateParameters: T[] = []
    if (
      Array.isArray(this.options.templateParameters) &&
      this.options.templateParameters.length > 0
    ) {
      // Shuffle columns independently, if requested
      const shuffleTable =
        Array.isArray(this.options.shuffleGroups) &&
        this.options.shuffleGroups.length

      const shuffledParameters = shuffleTable
        ? this.random.shuffleTable(
            this.options.templateParameters,
            this.options.shuffleGroups,
            this.options.shuffleUngrouped,
          )
        : this.options.templateParameters

      // Sample parameters
      templateParameters = sampleMode(
        this.random,
        shuffledParameters,
        this.options.sample.n,
        this.options.sample.mode,
      )
    } else {
      console.warn(
        'Empty or invalid parameter set for loop, no content generated',
      )
    }

    // Generate the content by cloning the template,
    // replacing the parameters each time, or by
    // mapping the parameters onto a function that
    // returns a component.
    const template = this.options.template
    if (template instanceof Component) {
      this.options.content = templateParameters.map(p => {
        const c = clone(template)
        // Extend parameters
        c.options.parameters = {
          ...c.options.parameters,
          ...p,
        }
        return c
      })
    } else if (Array.isArray(template)) {
      this.options.content = templateParameters.flatMap(p =>
        template.map((t: Component) => {
          const c = clone(t)
          c.options.parameters = {
            ...t.options.parameters,
            ...p,
          }
          return c
        }),
      )
    } else if (isFunction(template)) {
      this.options.content = templateParameters.map((p, i) =>
        template(p, i, this),
      )
    } else {
      console.warn('Missing or invalid template in loop, no content generated')
    }

    return super.onPrepare()
  }
}

Loop.metadata = {
  module: ['flow'],
  nestedComponents: ['template'],
  parsableOptions: {
    templateParameters: {
      type: 'array',
      content: {
        // Parse the values of objects in
        // the templateParameters array
        content: {
          '*': {},
        },
      },
    },
    sample: {
      type: 'object',
      content: {
        n: { type: 'number' },
        replace: { type: 'boolean' },
        mode: {},
      },
    },
  },
}
